<template>
  <div id="astro">
    <div class="content">
      <van-row style="flex" justify="space-around">
        <van-col
          span="9"
          class="tag"
          v-for="(item,index) of astro"
          :key="index"
          @click="detail(item.astroid)"
        >
          <div class="title">{{item.astroname}}</div>
          <img :src="item.pic" />
          <div class="date">{{item.date}}</div>
        </van-col>
      </van-row>
    </div>
    <!-- 覆盖层 -->
    <van-overlay :show="show" @click="show = false;fortune={}">
      <div class="wrapper">
        <div class="deltitle">{{fortune.astroname}}</div>
        <div class="today" v-if="fortune.today != undefined">
          <van-divider class="subtitle">今日运势</van-divider>
          <div class="delItem"><span>-幸运色-：</span>{{fortune.today.color}}</div>
          <div class="delItem"><span>-健康指数-：</span>{{fortune.today.health}}</div>
          <div class="delItem"><span>-爱情指数-：</span>{{fortune.today.love}}</div>
          <div class="delItem"><span>-财运指数-：</span>{{fortune.today.money}}</div>
          <div class="delItem"><span>-幸运数字-：</span>{{fortune.today.number}}</div>
          <div class="delItem"><span>-匹配星座-：</span>{{fortune.today.star}}</div>
          <div class="delItem"><span>-今日运势-：</span>{{fortune.today.presummary}}</div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>
<script>
export default {
  data() {
    return {
      appkey: "8f8aa94f10789724",
      astro: [],
      fortune: {},
      show: false
    };
  },
  mounted() {
    let date = new Date()
      .toLocaleDateString()
      .split("/")
      .join("-");
    // console.log(date);
    let url = `/astro/all?appkey=${this.appkey}`;
    this.axios.get(url).then(res => {
      this.astro = res.data.result;
      // console.log(this.astro);
    });
  },
  methods: {
    detail(i) {
      this.show = true;
      let date = new Date().toLocaleDateString();
      let url = `/astro/fortune?astroid=${i}&date=${date}&appkey=${this.appkey}`;
      this.axios.get(url).then(res => {
        this.fortune = res.data.result;
        console.log(this.fortune);
      });
    }
  }
};
</script>
<style scoped>
#astro {
  padding-bottom: 65px;
  width: 100%;
}

.content {
  padding: 0 5px;
}

.item {
  margin-top: 5px;
}

.astroItem {
  margin-bottom: 5px;
  border-bottom: 1px dashed #787878;
}

/* .astro {
  margin-bottom: 15px;
  padding-bottom: 10px;
} */

.title {
  margin-top: 30%;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}

.tag {
  margin-left: 8%;
  text-align: center;
}

.tag img {
  border-radius: 50%;
}
.tag .date {
  background-color: sandybrown;
  border-radius: 8px;
}

.wrapper {
  width: 100%;
  height: 100%;
  padding: 15px 10px 65px;
  box-sizing: border-box;
  background-color: #fff;
  color:darkslategrey;
  line-height: 26px;
}

.deltitle {
  color: red;
  text-align: center;
  font-size: 22px;
  font-weight: bold;
}

.subtitle {
  font-size: 16px;
  font-weight: bold;
  color: brown;
}

.delItem {
  margin-bottom: 10px;
}
.delItem span {
  color: lightslategray;
  font-weight: bold;
}
</style>