import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import MintUI from 'mint-ui'
import axios from 'axios'

import Tabbar from './components/Tabbar'

import Router from 'vue-router'
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}

import 'mint-ui/lib/style.min.css';
Vue.use(MintUI);

// Vant组件
import { Dialog, Button, Divider, Overlay, Field, NoticeBar, Toast, Empty, Tag, PullRefresh, List, DatetimePicker, Search, Swipe, SwipeItem, Lazyload, Grid, GridItem, Popup, Cell, CellGroup, Row, Col, Tab, Tabs } from 'vant';
Vue.use(DatetimePicker);
Vue.use(Search);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Popup);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Row);
Vue.use(Col);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(List);
Vue.use(PullRefresh);
Vue.use(Tag);
Vue.use(Empty);
Vue.use(NoticeBar);
Vue.use(Field);
Vue.use(Overlay);
Vue.use(Divider);
Vue.use(Button);
Vue.use(Dialog);

// 引入图标样式
import './font/iconfont.css'

// 配置axios访问服务器的基础路径
// axios.defaults.baseURL = "http://127.0.0.1:4000/";
// // 配置axios的session,让axios携带凭证数据
axios.defaults.withCredentials = true;
// // 注册axios
Vue.prototype.axios = axios;

Vue.config.productionTip = false

Vue.component('my-tabbar', Tabbar);

new Vue({
  router,
  store,
  axios,
  render: h => h(App)
}).$mount('#app')
