<template>
  <div id="index">
    <mt-header fixed title="首页"></mt-header>
    <div class="notice">
      <van-notice-bar
        text="通知内容"
        background="#ecf9ff"
        left-icon="https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1042768786,526816433&fm=26&gp=0.jpg"
      >
        <iframe
          name="weather_inc"
          src="http://i.tianqi.com/index.php?c=code&id=1"
          style="margin: 40px auto -50px; padding: 0 15px"
          width="340"
          height="35"
          frameborder="0"
          marginwidth="0"
          marginheight="0"
          scrolling="no"
        ></iframe>
      </van-notice-bar>
    </div>
    <!-- 搜索框 -->
    <van-search
      v-model="value"
      shape="round"
      placeholder="请输入搜索关键词"
      input-align="center"
      background="#4fc08d"
      @input="search"
    />

    <!-- 轮播 -->
    <van-swipe class="my-swipe" :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>

    <!-- 功能按钮 -->
    <van-grid>
      <van-grid-item
        icon="https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1042768786,526816433&fm=26&gp=0.jpg"
        text="本地天气"
        @click="weather"
      />
      <van-grid-item
        icon="https://dss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2691982962,2438500774&fm=26&gp=0.jpg"
        text="新闻"
        @click="news"
      />
      <van-grid-item
        icon="https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=150110493,1247035460&fm=26&gp=0.jpg"
        text="收藏"
        @click="collect"
      />
      <van-grid-item
        icon="http://img2.imgtn.bdimg.com/it/u=1191183082,2907425704&fm=26&gp=0.jpg"
        text="没有了"
      />
    </van-grid>
    <van-tabs color="blcak" v-model="active" duration="0.5" class="content">
      <van-tab title="今日运势" class="item astro">
        <my-top />
      </van-tab>
      <van-tab title="智能机器人" class="item collectItem">
        <my-iqa />
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import myTop from "../components/Astro";
import myIqa from "../components/Iqa";
export default {
  data() {
    return {
      result: [],
      value: "",
      timer: "",
      images: [
        "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1727742408,1088965637&fm=26&gp=0.jpg",
        "https://dss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=4030137750,3670891289&fm=26&gp=0.jpg",
        "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1777208049,1819793330&fm=26&gp=0.jpg",
        "https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3390123055,3833905287&fm=26&gp=0.jpg"
      ],
      active: "新闻速览",
    };
  },
  components: {
    myTop,
    myIqa
  },
  mounted() {
    // console.log("挂载首页");
    let url =
      "/api/rcd?cid=56&ext=games&token=c786875b8e04da17b24ea5e332745e0f&num=20&expIds=20190106A13PFT%7C20190108A04MLS&page=0";
    this.axios.get(url).then(res => {
      // console.log(res.data);
    });
  },
  beforeRouteEnter(to, from, next) {
    // console.log("to", to);
    // 如果从首页跳转到products是不需要缓存的
    if (from.name === "weather") {
      // console.log(to.meta.keepAlive);
      to.meta.keepAlive = true;
      // console.log(to.meta.keepAlive);
    }
    // console.log("from", from);
    // console.log("进入首页");
    next();
  },
  beforeRouteLeave(to, from, next) {
    // console.log("离开商品列表页面");
    next();
  },
  methods: {
    search() {
      // 节流防抖
      if (this.timer != "") {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        const val = this.value;
        if (val.trim() != "") {
          alert(`搜索${val}`);
        }
      }, 1000);
    },
    weather() {
      // console.log("天气");
      this.$store.commit("updateSelected", "weather");
      this.$router.push("/weather");
    },
    news() {
      // console.log("新闻");
      this.$store.commit("updateSelected", "news");
      this.$router.push("/news");
    },
    collect() {
      // console.log("新闻");
      this.$store.commit("updateSelected", "collect");
      this.$router.push("/collect");
    }
  }
};
</script>
<style scoped>
.notice {
  margin-top: 40px;
}

.my-swipe {
  padding-top: 5px;
  text-align: center;
  border: 2px solid antiquewhite;
}
.my-swipe img {
  height: 215.63px;
}
</style>