<template>
  <div id="iqa">
    <van-cell-group>
      <van-field
        v-model="value"
        placeholder="请输入您的问题"
        right-icon="https://dss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2903898918,1941161410&fm=26&gp=0.jpg"
        class="input"
        @click-right-icon="answer"
      />
    </van-cell-group>
    <div id="chat"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      appkey: "8f8aa94f10789724",
      value: ""
    };
  },
  methods: {
    answer() {
      const que = document.createElement("div");
      const ans = document.createElement("div");
      const chat = document.getElementById("chat");
      que.className = "que";
      que.style = "color:cadetblue;margin:5px 0 5px";
      ans.className = "ans";
      ans.style = "color:burlywood;margin-bottom:15px";
      que.innerHTML = `Q：${this.value}`;
      if (this.value.trim() != "") {
        chat.appendChild(que);
        // console.log(this.value);
        let url = `/iqa?appkey=${this.appkey}&question=${this.value}`;
        this.axios.get(url).then(res => {
          // console.log(res.data.result.content);
          ans.innerHTML = `A：${res.data.result.content}`;
          chat.appendChild(ans);
          this.value = "";
        });
      }
    }
  }
};
</script>
<style scoped>
.input {
  text-align: center;
}

#chat {
  padding: 5px 5px 70px;
  font-size: 16px;
  line-height: 22px;
  color: cadetblue;
}
</style>