<template>
  <div class="index">
    <mt-tabbar v-model="selected" fixed>
      <mt-tab-item id="index">
        <img src="../assets/images/index.png" slot="icon" v-if="selected != 'index'" />
        <img src="../assets/images/index_active.png" slot="icon" v-if="selected == 'index'" />
        首页
      </mt-tab-item>
      <mt-tab-item id="weather">
        <img src="../assets/images/weather.png" slot="icon" v-if="selected != 'weather'" />
        <img src="../assets/images/weather_active.png" slot="icon" v-if="selected == 'weather'" />
        天气
      </mt-tab-item>
      <mt-tab-item id="news">
        <img src="../assets/images/news.png" slot="icon" v-if="selected != 'news'" />
        <img src="../assets/images/news_active.png" slot="icon" v-if="selected == 'news'" />新闻
      </mt-tab-item>
      <mt-tab-item id="collect">
        <img src="../assets/images/collect.png" slot="icon" v-if="selected != 'collect'" />
        <img src="../assets/images/collect_active.png" slot="icon" v-if="selected == 'collect'" />收藏
      </mt-tab-item>
      <mt-tab-item id="user">
        用户
        <img src="../assets/images/user.png" slot="icon" v-if="selected != 'user'" />
        <img src="../assets/images/user_active.png" slot="icon" v-if="selected == 'user'" />
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selected: ""
    };
  },
  mounted() {
    this.selected = this.$store.state.selected;
    this.$router.push(this.selected);
  },
  computed: {
    selectedchange() {
      return this.$store.state.selected;
    }
  },
  watch: {
    selected() {
      this.$store.commit("updateSelected", this.selected);
      this.$router.push(this.selected);
    },
    selectedchange() {
      this.selected = this.selectedchange;
    }
  }
};
</script>
<style scoped>
.index {
  z-index: 1000000 !important;
}
</style>