<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/login">Login</router-link> |
      <router-link to="/product">Product</router-link> |
    </div>-->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive" />
    <my-tabbar v-show="!(path ==='/details')"></my-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      path: ""
    };
  },
  // 判断路由
  mounted() {
    this.path = this.$route.path;
    // console.log(this.$route.path)
    const collect = window.localStorage.getItem("collect");
    if (collect === null) {
      window.localStorage.setItem("collect", JSON.stringify([]));
    }
  },
  watch: {
    $route(to, from) {
      this.path = to.path;
    }
  }
};
</script>

