import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/index',
    component: Index,
    meta: {
      keepAlive: false
    }
  },
  {
    path: '/index',
    name: "index",
    component: Index
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import (/*webpackChunkName: "register"*/ '../views/Register')
  },
  {
    path: '/user',
    name: 'user',
    component: () => import (/*webpackChunkName: "user"*/ '../views/User')
  },
  {
    path: '/weather',
    name: 'weather',
    component: () => import (/*webpackChunkName: "weather"*/ '../views/Weather')
  },
  {
    path: '/news',
    name: 'news',
    component: () => import (/*webpackChunkName: "news"*/ '../views/News'),
    meta: {
      keepAlive: false
    }
  },
  {
    path: '/details',
    name: 'details',
    component: () => import (/*webpackChunkName: "details"*/ '../views/Details')
  },
  {
    path: '/collect',
    name: 'collect',
    component: () => import (/*webpackChunkName: "collect"*/ '../views/Collect')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
